import React from 'react';
import Types from 'prop-types';

import './InstructionList.less';

const DosAndDonts = ({ dos, donts }) => (
  <div className="instructions">
    {dos.map((doThis, index) => (
      // eslint-disable-next-line react/no-array-index-key
      <div className="instruction m-t-1" key={index}>
        <svg className="do" width="22" height="22" xmlns="http://www.w3.org/2000/svg">
          <g fill="#2ED06E" fillRule="evenodd">
            <path d="M11 1C5.486 1 1 5.486 1 11s4.486 10 10 10 10-4.486 10-10S16.514 1 11 1m0 21C4.935 22 0 17.065 0 11S4.935 0 11 0s11 4.935 11 11-4.935 11-11 11" />
            <path d="M9.324 14.697l-3.182-3.099.699-.716 2.484 2.42 5.836-5.66.696.716z" />
          </g>
        </svg>
        <p className="m-l-2 m-b-0 text-primary">{doThis}</p>
      </div>
    ))}
    {donts.map((dont, index) => (
      // eslint-disable-next-line react/no-array-index-key
      <div className="instruction m-t-1" key={index}>
        <svg className="dont" width="22" height="22" xmlns="http://www.w3.org/2000/svg">
          <g fill="#F53636" fillRule="evenodd">
            <path d="M11 1C5.486 1 1 5.486 1 11s4.486 10 10 10 10-4.486 10-10S16.514 1 11 1m0 21C4.935 22 0 17.065 0 11S4.935 0 11 0s11 4.935 11 11-4.935 11-11 11" />
            <path d="M15.85 6.854l-.706-.708-4.147 4.147L6.85 6.147l-.707.707 4.146 4.145-4.146 4.147.707.707 4.146-4.146 4.147 4.146.707-.707L11.705 11z" />
          </g>
        </svg>
        <p className="m-l-2 m-b-0 text-primary">{dont}</p>
      </div>
    ))}
  </div>
);

DosAndDonts.defaultProps = {
  dos: [],
  donts: [],
};

DosAndDonts.propTypes = {
  dos: Types.arrayOf(Types.string),
  donts: Types.arrayOf(Types.string),
};

export default DosAndDonts;



